*,
*::before,
*::after {
  content: none;
  box-sizing: border-box;
  column-rule: 0 solid;
}
@media print {
  *,
  *::before,
  *::after {
    background: transparent !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
}
*:not(input):not(button):not(select),
*::before,
*::after {
  border: 0 solid;
}
